import React from "react"
import Carousel from "./Carousel"
import ScrollableAnchor from 'react-scrollable-anchor'
class Projects extends React.Component{
	constructor(){
		super()
		this.state = {
		    projects: [
		    	{
		    		header: "Table For Two", 
		    		desc: "Table For Two is a web application for posting, searching, and matching with user created events. I built it full stack using ReactJS for the front end, and a NodeJS Express server using the 'Sequelize' ORM for the backend. I used Amazon Web Services to host the servers and a mySQL database. The app also uses Socket.io to implement real time chat rooms. It also uses the imgur APIs to host images for events, and stores profile pictures in base64 encoded BLOBs on the database, cropping and compressing using npm modules. Here are some features implemented in the site: ",
		    		slides: [
		    			{img: require("../Assets/TableForTwo/homepage_1.PNG"), desc: "Homepage of Table For Two, featuring events added from different users than the current user. Events are shown in a Carousel."},
		    			{img: require("../Assets/TableForTwo/homepage_2.PNG"), desc: "Showing newest event in carousel."},
		    			{img: require("../Assets/TableForTwo/homepage_3.PNG"), desc: "Event display as user scrolls."},
		    			{img: require("../Assets/TableForTwo/dash_1.PNG"), desc: "Once logged in, a user can add/edit/delete their hosted events, and invite/uninvite people who 'Liked' their events. Events are stored in the database, where images are uploaded to imgur APIs and the URL is stored with the event"},
		    			{img: require("../Assets/TableForTwo/dash_2.PNG"), desc: "Form to create or edit an event"},
		    			{img: require("../Assets/TableForTwo/profile_1.PNG"), desc: "Users can update their profile, Profile images are base64 encoded, compressed, and cropped using 'imagemin' and 'sharp' node modules respectively."},
		    			{img: require("../Assets/TableForTwo/match_1.PNG"), desc: "Once logged in, user can 'Like' Bob's event and send a message."},
		    			{img: require("../Assets/TableForTwo/match_2.PNG"), desc: "Bob can see who liked his event once logged in."},
		    			{img: require("../Assets/TableForTwo/match_3.PNG"), desc: "Bob clicks on the user bubble to see full profile, and can invite user to join event."},
		    			{img: require("../Assets/TableForTwo/chat_1.PNG"), desc: "Once a user has matched with an event and been invited by the event creator, they are entered into a chat with everyone invited to that event. The chat logs can be stored in the database, but it is currently disabled. The chat works using the 'socket.io' package. The UI for the chat page is unfinished and still rough, but users can change chatrooms and view event details and chat members."},
		    			{img: require("../Assets/TableForTwo/chat_2.PNG"), desc: "Chat sidebar folds down on click to reveal event info"},
		    		]
		    	},
		    	{
		    		header: "News Polling Backend", 
		    		desc: (<p>RESTful API backend build with NodeJS Express server, sequelize ORM, and mySQL database, all deployed on AWS. Web application built for a client who wanted creatable polls on his website. Backend supports Create/Read/Update/Delete functionality for new questions with variable number of options, as well as users casting votes, and getting a map of votes mapped to the state the vote came from </p>),
		    	},
		    	{
		    		header: "NodeJS RESTful API", 
		    		desc: (<p>I have created APIs for multiple web apps featuring: custom database, password encryption, java web token authentication and account verification, server side image storage and upload, and many more small details as the project requires. <br /> due to the privacy of the applications I will not post full source code, but sample code can be provided on request</p> ),
		    	},
		    	{
		    		header: "Be'Chol Lashon Charity Website", 
		    		desc:(<p> various work (including custom plugins) for a Wordpress website. I created the backend for a custom search plugin to search the extensive archive database using WP_Query and custom metadata filters. I also made a custom plugin to handle pagination requests from all category pages and search pages. In addition to the plugins, I did various backend work and created numerous features</p>),
		    	},
		    	{
		    		header: "RentSFSU", 
		    		desc: (<p> This project was made by a group of five developers as part of the capstone class for Computer Science majors. The website was created to match student renters with apartment renters.<br />    My role in this project was backend developer, mainly writing the model and controller for apartment listings. I also implemented google maps API to get the coordinates of apartments and used BLOBs to store images on the database.<br />    The website was written in PHP using an MVC framework  with a SQL database and hosted on Amazon web servers.<br/>    Here are some images illustrating the functionality of my backend contributions to the website: </p>),
		    		slides: [
		    			{img: require("../Assets/RentSFSU/rentSFSU_1.png"), desc: "RentSFSU homepage"},
		    			{img: require("../Assets/RentSFSU/rentSFSU_2.png"), desc: "Page displaying all relevant listings"},
		    			{img: require("../Assets/RentSFSU/rentSFSU_3.png"), desc: "Listing creation page"},
		    			{img: require("../Assets/RentSFSU/rentSFSU_4.png"), desc: "Using Google APIs to display approximate location of property, to maintain security of listing"}
		    		]
		    	},
		    	{
		    		header: "Tank and plane games", 
		    		desc: <p> The first large programming project I did was creating these two games in Java. Other than the existing image assets, the two games were built from scratch and share some reusable Java classes.<br />    Here are some screenshots of the two games:</p>,
		    		slides: [
		    			{img: require("../Assets/PlaneTank/PlaneGame_1.png"), desc: "Gameplay of the plane game"},
		    			{img: require("../Assets/PlaneTank/PlaneGame_2.png"), desc: "The game features randomly spawning enemies, and two player co-op to progress as far as possible until one player is out of health"},
		    			{img: require("../Assets/PlaneTank/TankGame_1.png"), desc: "Gameplay of the tank game"},
		    			{img: require("../Assets/PlaneTank/TankGame_3.png"), desc: "The game features player vs player combat, with destructable walls, splitscreen cameras, and a realtime minimap"},
					]
		    	},
		    ]
		}
	}

	render(){
		let projects = this.state.projects.map((project, index) =>{
			return(
				<div className="project-container">
					<ScrollableAnchor id ={'project' + index}>
						<h3 className="project-header">{project.header}</h3>
					</ScrollableAnchor>
					<p className="project-desc">{project.desc}</p>
					{project.slides &&
						<Carousel slides={project.slides} />
					}
				</div>
			)
		})
		let anchorLinks = this.state.projects.map((project, index) =>{
			return(
				<a href={'#project' + index}> {project.header} </a>
			)
		})
		let refs = this.state.projects.map((project, index) =>{
			return({ref: '#project' + index, name: project.header})
		})
		this.props.setLinks(refs)
		return(
			<div className="projects">
				<h2> Projects </h2>
				{projects}
			</div>
		)
	}
}

export default Projects