import React from "react"
class ParalaxBackground extends React.Component{
	constructor(){
		super()
		this.state = {
			testproject: [
    			{img: require("../Assets/TableForTwo/homepage_3.PNG")},
    			{img: require("../Assets/TableForTwo/dash_1.PNG")},
    			{img: require("../Assets/TableForTwo/dash_2.PNG")},
    			{img: require("../Assets/TableForTwo/profile_1.PNG")},
    			{img: require("../Assets/TableForTwo/match_1.PNG")},
    			{img: require("../Assets/TableForTwo/match_2.PNG")},
    			{img: require("../Assets/TableForTwo/match_3.PNG")},
    			{img: require("../Assets/TableForTwo/chat_2.PNG")},
    			{img: require("../Assets/TableForTwo/homepage_1.PNG")},
	    		{img: require("../Assets/RentSFSU/rentSFSU_1.png")},
    			{img: require("../Assets/RentSFSU/rentSFSU_2.png")},
    			{img: require("../Assets/RentSFSU/rentSFSU_3.png")},
    			{img: require("../Assets/RentSFSU/rentSFSU_4.png")},
   				{img: require("../Assets/PlaneTank/PlaneGame_2.png")},
   				{img: require("../Assets/PlaneTank/PlaneGame_4.png")},
   				{img: require("../Assets/PlaneTank/TankGame_1.png")},
   				{img: require("../Assets/PlaneTank/TankGame_3.png")},

		    		
		    ],
		    tiles: [0,0,0,0,0,0,0,0],
		    width: window.innerWidth,
		    freeImages: [],
		}
		let freeImages = this.state.testproject.map((content, index) => {
			return index
		})
		this.state.freeImages = freeImages

		this.setNext = this.setNext.bind(this)
		this.updateWindowDimensions = this.updateWindowDimensions.bind(this)
		this.initTiles = this.initTiles.bind(this)
	}
	componentDidMount(){
		this.initTiles()
		this.updateWindowDimensions();
		window.addEventListener('resize', this.updateWindowDimensions);
		this.interval = setInterval(() => {this.setNext()}, 5000)
	}
	componentWillUnmount(){
 		window.removeEventListener('resize', this.updateWindowDimensions);
		clearInterval(this.interval);
	}
	updateWindowDimensions() {
		//XOR check of state vs window only triggers on media size change
		if((this.state.width < 1350)? !(window.innerWidth <1350): (window.innerWidth <1350)){
			let freeImagesTemp = this.state.freeImages.slice()
			let tilesTemp = this.state.tiles.slice()
			//IF WINDOW GETS BIGGER fill in new squares
			if(window.innerWidth > 1350){
				for(let i = 6; i < 8; i++){
					let randomImage = Math.floor(Math.random() * (freeImagesTemp.length - 1))
					tilesTemp[i] = freeImagesTemp[randomImage]
					freeImagesTemp = freeImagesTemp.filter((img) =>{
						return img !== tilesTemp[i]
					})
				}			
				this.setState({tiles: tilesTemp, freeImages: freeImagesTemp, width: window.innerWidth})		
			//IF WINDOW GETS SMALLER free up square's images getting pushed out
			}else{
				for(let i = 6; i < 8; i++){
					freeImagesTemp.push(tilesTemp[i])
					this.setState({freeImages: freeImagesTemp, width: window.innerWidth})		
				}	
			}
		}else{
			this.setState({ width: window.innerWidth});
		}
	}
	initTiles(){
		let freeImagesTemp = this.state.freeImages.slice()
		let tileNum = this.state.width > 1350? 8: 6
		let newTiles = this.state.tiles.map((tile, index)=> {
			let randomFreeImage = Math.floor(Math.random() * (freeImagesTemp.length))
			let image = freeImagesTemp[randomFreeImage]
			//Don't count tiles that are off screen
			if(index < tileNum){
				freeImagesTemp = freeImagesTemp.filter((img) =>{
					return img !== image
				})
				console.log({image})
				return image
			}else{
				return image
			}
		})
		console.log(newTiles)
		this.setState({freeImages: freeImagesTemp, tiles:newTiles})
	}
	setNext(index){
		let tileNum = this.state.width > 1350? 8: 6
		let randomTile = index? index: Math.floor(Math.random() * (tileNum))
		let randomImage = Math.floor(Math.random() * (this.state.freeImages.length - 1))
		this.setState((prevState) => {
			let newTiles = prevState.tiles
			let newFreeImages = prevState.freeImages
			newFreeImages.push(newTiles[randomTile])
			newTiles[randomTile] = prevState.freeImages[randomImage]
			newFreeImages = newFreeImages.filter((img) =>{
				return img !== newTiles[randomTile]
			})
			return {tiles: newTiles, freeImages: newFreeImages}
		})
	}

	render(){

		// let pictures = this.state.testproject.map((img) => {
		// 	return(
		// 		<div className="image-element-class">
		// 			<img src={img.img} />
		// 		</div>
		// 	)
		// })
		// let pictures = this.state.testproject.map((img) => {
		// 	return(
		// 		<div className="image-element-class">
		// 			<img src={require(img.img)} />
		// 		</div>
		// 	)
		// })
		let tiles
		if(this.state.width > 1350){
			tiles = this.state.tiles.map((next, index) => {
				return(<Tile 
					image={this.state.testproject[next].img} 
					next={this.state.testproject[next].img} 
					key={index} 
					index={index} 
					nextCallback={this.setNext} />)
			})
		}else{
			tiles = this.state.tiles.slice(0, 6).map((next, index) => {
				return(<Tile 
					image={this.state.testproject[next].img} 
					next={this.state.testproject[next].img} 
					key={index}
					index={index} 
					nextCallback={this.setNext} />)
			})			
		}
		return(
			<div className="paralax-background">
				<div className="background-content">
					<div className="tile-box">
						{tiles}
					</div>
				</div>
			</div>
		)
	}
}

class Tile extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			front: props.image,
			back: props.next,
			flipped: false
		}
		this.flipCard = this.flipCard.bind(this)
	}
	async componentDidUpdate(prevProps){
		if(this.props.next !== this.state.front && this.props.next !== this.state.back){
			if(this.state.flipped){
				this.setState({front: this.props.next})
			}else{
				this.setState({back: this.props.next})
			}
			this.flipCard()
		}
		// if(this.props.next !== this.state.front && this.props.next !== this.state.back){
		// 	var nextImage = new Image()
		// 	nextImage.src = this.props.next
		// 	nextImage.onload = () => {
		// 		if(this.state.flipped){
		// 			this.setState({front: nextImage.src})
		// 		}else{
		// 			this.setState({back: nextImage.src})
		// 		}
		// 		this.flipCard()
		// 	}
		// }
	}
	flipCard(){
		this.setState((prevState) => {
			return {flipped: !prevState.flipped}
		})
	}
	render(){

		return(
			<div className="tile" onClick={() => {this.props.nextCallback(this.props.index)}}>
				<div className="flip-card">
					<div id="flip-box" className={this.state.flipped? "flip-card-inner flipped": "flip-card-inner"}>
						<img src={this.state.front} style={{visibility: "hidden"}}/>
						<div className="flip-card-front" style={{
							backgroundImage: `url(${this.state.front})`,
							backgroundRepeat: "no-repeat",
							backgroundSize: "cover"
						}}>
						</div>
						<div className="flip-card-back" style={{
							backgroundImage: `url(${this.state.back})`,
							backgroundRepeat: "no-repeat",
							backgroundSize: "cover"
						}}>
						</div>
					</div>
				</div>
			</div>
		)
	}
}
export default ParalaxBackground