import React from "react"

class ParalaxSlider extends React.Component{
	constructor(){
		super()

		this.state = {
			current: 0,
			next: 1,
			images: [
   				{img: require("../Assets/PlaneTank/PlaneGame_2.png")},
    			{img: require("../Assets/TableForTwo/homepage_1.PNG")},
       			{img: require("../Assets/postman.PNG")},
	    		{img: require("../Assets/RentSFSU/rentSFSU_1.png")},
    			{img: require("../Assets/TableForTwo/dash_1.PNG")},
   				{img: require("../Assets/PlaneTank/TankGame_1.png")},
    			{img: require("../Assets/TableForTwo/chat_2.PNG")},
   				{img: require("../Assets/PlaneTank/PlaneGame_4.png")},
    			{img: require("../Assets/TableForTwo/profile_1.PNG")},
   				{img: require("../Assets/PlaneTank/TankGame_3.png")},
    			{img: require("../Assets/TableForTwo/match_3.PNG")},
			],
			reset:false
		}

		this.setNext = this.setNext.bind(this)
	}
	componentDidMount(){
		this.interval = setInterval(() => {this.setNext()}, 10000)
	}
	setNext(){
 		var element = document.getElementById("slider");
 		element.classList.remove("slider-animator");
		this.setState((prevState) => {
			let nextSlide = (prevState.next + 1) % prevState.images.length
			return {current: prevState.next, next: nextSlide, reset: !prevState.reset}
		})
		setTimeout(function(){element.classList.add("slider-animator")},1)
	}
	componentWillUnmount(){
		clearInterval(this.interval);
	}
	render(){
		return(
			<div className="slider-background">
				<div className="slider-container"> 
					<div id="slider" className="slider slider-animator" style={{transform: "translateX(0)"}}>
						<div className="current paralax-slide" style={{
							backgroundImage: `url(${this.state.images[this.state.current].img})`,
							backgroundRepeat: "no-repeat",
							backgroundSize: "contain"
						}}></div>
						<div className="next paralax-slide" style={{
							backgroundImage: `url(${this.state.images[this.state.next].img})`,
							backgroundRepeat: "no-repeat",
							backgroundSize: "contain"
						}}></div>
					</div>
					<div className="border">
						<div className="shadow-left"></div>
						<div className="shadow-right"></div>
					</div>
					<div className="cover-text">
						<h2>Josh Rubin </h2> <h3> Web Development and Back End Engineering</h3>
					</div>
				</div>
			</div>
		)
	}
}

export default ParalaxSlider